#root {
  width: 100%;
  height: 100vh;
}

/* Custom animations for the glowing button */
@keyframes glow {
  0%, 100% {
    box-shadow: 0 0 50px 10px rgba(45, 212, 191, 0.5);
  }
  50% {
    box-shadow: 0 0 80px 20px rgba(45, 212, 191, 0.8);
  }
}

@keyframes shimmer {
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(100%);
  }
}

@keyframes pulse-glow {
  0%, 100% {
    opacity: 0.6;
    transform: scale(1);
  }
  50% {
    opacity: 1;
    transform: scale(1.05);
  }
}

.animate-glow {
  animation: glow 2.5s ease-in-out infinite;
}

.animate-shimmer {
  animation: shimmer 3s linear infinite;
}

.animate-pulse-glow {
  animation: pulse-glow 5s ease-in-out infinite;
}

/* ============ Premium Teal Headline Effects ============ */
@keyframes teal-gradient-shift {
  0% { background-position: 0% 50%; }
  50% { background-position: 100% 50%; }
  100% { background-position: 0% 50%; }
}

@keyframes teal-text-glow {
  0%, 100% {
    text-shadow:
      0 0 10px rgba(20, 184, 166, 0.35),
      0 0 22px rgba(20, 184, 166, 0.25),
      0 0 36px rgba(34, 211, 238, 0.15);
  }
  50% {
    text-shadow:
      0 0 14px rgba(20, 184, 166, 0.55),
      0 0 28px rgba(20, 184, 166, 0.35),
      0 0 52px rgba(34, 211, 238, 0.25);
  }
}

@keyframes teal-underline-sweep {
  0% { transform: translateX(-110%) scaleX(0.6); opacity: 0; }
  20% { opacity: 1; }
  60% { transform: translateX(10%) scaleX(1); opacity: 1; }
  100% { transform: translateX(110%) scaleX(0.6); opacity: 0; }
}

/* Animated teal gradient text */
.animated-teal-gradient {
  /* Dark-to-light teal gradient sweep */
  background: linear-gradient(90deg, #0f766e, #14b8a6, #5eead4, #14b8a6, #0f766e);
  background-size: 200% 200%;
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  animation: teal-gradient-shift 6s ease-in-out infinite;
}

/* Soft shimmering teal glow on text */
.teal-glow-text {
  animation: teal-text-glow 3.2s ease-in-out infinite;
}

/* Underline sweep accent beneath highlighted words */
.teal-underline {
  position: relative;
}
.teal-underline::after {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  bottom: -0.2em;
  height: 0.35em;
  border-radius: 9999px;
  background: radial-gradient(60% 100% at 50% 50%, rgba(20,184,166,0.45), rgba(34,211,238,0.25), rgba(20,184,166,0));
  filter: blur(4px);
  animation: teal-underline-sweep 2.4s ease-out infinite;
}

/* Optional subtle sparkle dots container for magical feel */
.sparkle-container {
  position: relative;
}
.sparkle-dot {
  position: absolute;
  width: 6px;
  height: 6px;
  border-radius: 9999px;
  background: radial-gradient(circle, rgba(255,255,255,0.9) 0%, rgba(20,184,166,0.9) 40%, rgba(20,184,166,0) 70%);
  filter: blur(0.5px);
  opacity: 0.75;
  animation: sparkle-float 4.6s ease-in-out infinite;
}
@keyframes sparkle-float {
  0% { transform: translateY(0) translateX(0) scale(0.9); opacity: 0.0; }
  10% { opacity: 0.8; }
  50% { transform: translateY(-10px) translateX(6px) scale(1.05); }
  90% { opacity: 0.8; }
  100% { transform: translateY(0) translateX(0) scale(0.9); opacity: 0.0; }
}

/* ===== Industries Page Animations ===== */
@keyframes grid-pan {
  0% { background-position: 0 0, 0 0; }
  100% { background-position: 60px 60px, -60px -60px; }
}

.industries-hero-grid {
  position: absolute;
  inset: 0;
  background-image:
    linear-gradient(rgba(255,255,255,0.06) 1px, transparent 1px),
    linear-gradient(90deg, rgba(255,255,255,0.06) 1px, transparent 1px);
  background-size: 30px 30px, 30px 30px;
  animation: grid-pan 20s linear infinite;
}

@keyframes dash {
  0% { stroke-dasharray: 0 30; stroke-dashoffset: 30; opacity: 0.6; }
  50% { stroke-dasharray: 30 0; stroke-dashoffset: 15; opacity: 1; }
  100% { stroke-dasharray: 30 0; stroke-dashoffset: 0; opacity: 0.9; }
}
.stream-line {
  stroke-linecap: round;
  stroke-linejoin: round;
  animation: dash 4.5s ease-in-out infinite alternate;
}

@keyframes dotPulse {
  0%, 100% { transform: scale(1); filter: drop-shadow(0 0 2px rgba(45,212,191,0.4)); }
  50% { transform: scale(1.08); filter: drop-shadow(0 0 6px rgba(34,211,238,0.6)); }
}
.stream-dot { animation: dotPulse 3.2s ease-in-out infinite; }
